@import url("https://fonts.googleapis.com/css2?family=Aldrich&family=Orbitron:wght@400;500;600;700;800;900&family=Rajdhani:wght@300;400;500;600;700&display=swap");
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
:root {
    --Aldrich: "Aldrich";
    --Orbitron: "Orbitron";
    --Rajdhani: "Rajdhani";
    --pink-light: #ff56f6;
    --purple: #b936ee;
    --blue-light: #3bace2;
    --blue-dark: #406aff;
}
html {
    scroll-behavior: smooth;
}
body {
    text-rendering: optimizeSpeed;
    background-image: url(../assets/img/bg.webp);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    font-family: var(--Orbitron);
}
.btn {
    background-image: linear-gradient(
        92.23deg,
        var(--pink-light) 21.43%,
        var(--purple) 50.63%,
        var(--blue-light) 100%,
        var(--blue-dark) 117.04%
    );
    box-shadow: 0px 4.42184px 107.23px rgba(255, 86, 246, 0.51);
    border-radius: 50px;
    color: #fff;
    padding: 15px 30px;
}
.btn:hover {
    background-image: linear-gradient(
        92.23deg,
        var(--blue-dark) 21.43%,
        var(--blue-light) 50.63%,
        var(--purple) 100%,
        var(--pink-light) 117.04%
    );
}
.background-clip-text {
    background-image: linear-gradient(
        to right,
        var(--pink-light),
        var(--blue-light)
    );
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
}
.df {
    display: flex;
}
.aic {
    align-items: center;
}
.jcc {
    justify-content: center;
}
.jcsb {
    justify-content: space-between;
}
.br50 {
    border-radius: 50%;
}
.pointer {
    cursor: pointer;
}
.section {
    height: 100vh;
}
.color-white {
    color: #fff;
}
.text-purple {
    text-transform: uppercase;
    color: var(--purple);
}
img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
a {
    text-decoration: none;
}
button,
input,
textarea {
    border: 0;
    outline: 0;
    font-family: var(--Orbitron);
    &:focus-visible {
        outline: 0;
        border: 0;
    }
}
.container {
    max-width: 1300px;
    padding: 0 15px;
    margin: auto;
}
