.information {
    width: 50%;
    text-align: left;
    h1,
    h3 {
        font-family: var(--Aldrich);
    }
    h1 {
        font-size: 50px;
        font-weight: 700;
    }
    h3 {
        font-size: 35px;
        font-weight: 600;
        span {
            margin-left: 10px;
        }
    }
    p {
        font-family: var(--Rajdhani);
        margin: 15px 0;
        font-size: 18px;
        line-height: 30px;
    }
    .social {
        margin-top: 30px;
        &-item {
            font-size: 20px;
            + .social-item {
                margin-left: 20px;
            }
        }
    }
}
.avatar {
    width: 40%;
    height: 500px;
    img {
        border-radius: 64% 36% 70% 30% / 30% 47% 53% 70%;
    }
}
