.contact {
    width: 1100px;
    .text,
    .form {
        width: 50%;
    }
    .text {
        text-align: left;
        p {
            font-size: 20px;
            margin-bottom: 10px;
            font-weight: bold;
        }
        h2 {
            font-size: 80px;
            width: 85%;
            font-family: var(--Rajdhani);
            font-weight: unset;
            letter-spacing: -2px;
        }
    }
    .form {
        padding: 20px 20px 50px;
        border: 1px solid #fff;
        border-radius: 10px;
        text-align: left;
        &-item {
            input,
            textarea {
                width: 100%;
                background-color: transparent;
                border-bottom: 1px solid #fff;
                padding: 20px 0;
                font-size: 18px;
                margin-top: 30px;
                &::placeholder {
                    color: rgb(255, 255, 255, 0.7);
                }
            }
            textarea {
                padding: 30px 0;
                resize: vertical;
                max-height: 300px;
                min-height: 100px;
            }
            &.button-contact {
                display: inline-block;
                margin-top: 50px;
            }
        }
    }
}
