.navigation {
    position: fixed;
    bottom: 5%;
    border-radius: 50px;
    background-color: rgb(51, 51, 51, 0.7);
    display: flex;
    align-items: center;
    justify-content: center;
    width: fit-content;
    margin: auto;
    left: 50%;
    transform: translateX(-50%);
    padding: 10px 20px;
    box-shadow: 0px 0 30px rgba(255, 224, 233, 0.09);
    .icon {
        color: #fff;
        width: 50px;
        height: 50px;
        transition: 0.5s;
        + .icon {
            margin-left: 10px;
        }
        &.active {
            background-image: linear-gradient(to right, #e500a4, #b100e8);
        }
    }
}
