.introduction {
    h2 {
        text-align: left;
        &:first-child {
            font-size: 25px;
            margin-bottom: 20px;
        }
        &:nth-child(2) {
            width: 70%;
            line-height: 40px;
            font-size: 30px;
            margin-bottom: 20px;
        }
    }
    .image {
        position: relative;
        width: 400px;
        height: auto;
        filter: opacity(0.6);
        margin-left: auto;
    }
}
.jobs {
    width: 100%;
    padding: 10px 10px 10px 70px;
    max-height: 600px;
    overflow: hidden scroll;
    &::-webkit-scrollbar {
        background-color: rgba(255, 255, 255, 0.2);
        border-radius: 10px;
        width: 5px;
    }
    &::-webkit-scrollbar-thumb {
        background-image: linear-gradient(
            to bottom,
            var(--blue-light),
            var(--pink-light)
        );
        border-radius: 10px;
    }
    .job {
        .text {
            width: 70%;
            padding-right: 30px;
            text-align: left;
            font-family: var(--Aldrich);
            h2 {
                padding-bottom: 15px;
            }
            p {
                line-height: 26px;
            }
        }
        .link {
            flex-direction: column;
            align-items: flex-end;
            .icon {
                width: 35px;
                height: 35px;
                background-image: linear-gradient(
                    to right,
                    var(--pink-light),
                    var(--blue-light)
                );
                border-radius: 50%;
                margin-bottom: 15px;
                svg {
                    transform: rotate(-45deg);
                }
            }
        }
        + .job {
            margin-top: 30px;
            padding-top: 30px;
            border-top: 2px solid rgba(255, 255, 255, 0.2);
        }
    }
}
