.avatar {
    width: 480px;
    height: 400px;
    img {
        border-radius: 20px;
    }
}
.information {
    width: 45%;
    text-align: left;
    h2 {
        font-size: 30px;
        &:nth-child(2) {
            font-weight: bold;
            margin-top: 30px;
            line-height: 45px;
        }
    }
    .desc {
        margin: 20px 0;
        width: 80%;
        line-height: 26px;
        letter-spacing: 1px;
    }
    .number {
        margin-bottom: 30px;
        &-item {
            width: 25%;
            font-weight: bold;
            p {
                &:first-child {
                    font-size: 38px;
                }
                &:last-child {
                    font-size: 18px;
                    margin-top: 10px;
                    letter-spacing: 1px;
                }
            }
            + .number-item {
                margin-left: 15px;
            }
        }
    }
}
